.App {
 
}

.App-header{
  width: 100%;
  height: 175px;
}

.App-logo {
  height: 120px;
  width: 120px;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

#navigation_container{
  margin: auto;
  width: 50%;
  padding: 10px;
}
#search_container{
  margin: auto;
  width: 50%;
  padding: 10px;
}
#search_container input[type="text"]{
  width: 100%;
  height: 25px;
  border: 2px;
  border-style: solid;
  border-radius: 15px;
  border-color: var(--COLOR_PANEL_BRD);
  background-color : var(--COLOR_SEARCH_INPUT_BG); 
  color: var(--COLOR_TEXT);
  font-family: "Consolas";
  font-size: 16px;
  padding: 10px;
  caret-color: red;
  caret-shape: block;

}



@keyframes cursor-blink {
  0% {
    opacity: 0;
  }
}





.label{
  color: var(--COLOR_TEXT);
}
.p-panel{
display: block;
margin: 10px;
width: 98%;
min-height: 250px;
border: 1px;
border-style: solid;
border-radius: 15px;
border-color: var(--COLOR_PANEL_BRD);
overflow: auto;
}

.p-contents{
  padding: 10px;
}

.p-title{
  display: block;
  align-items: center;
  color: var(--COLOR_TEXT);
  float: left;
  margin-right: 20px;
}

.p-chrome{
  display: block;
  padding: 10px;
  height: 25px;
  background-color: var(--COLOR_PANEL_CHROME_BG);
  margin-bottom: 20px;
}

.p-chrome button{
  display: block;
  align-self: left;
}

.p-chrome img{
  display: block;
  width: 25px;
  float: left;
  margin-right: 5px;
}

.w-container{
  display: block;
  float: left;
  width: 300px;
  height: 300px;
  border: 2px;
  border-style: solid;
  border-radius: 5px;
  margin: 10px;
  border-color: var(--COLOR_WIDGET_BRD);
  background-color: var(--COLOR_WIDGET_BG);
  position: relative;
}


.w-chrome{
  padding: 10px;
  height: 25px;
  background-color: var(--COLOR_PANEL_CHROME_BG);
  margin-bottom: 10px;
}

.w-title{
  display: block;
  width: 100%;
  align-items: center;
  color: var(--COLOR_TEXT);
}

.rotate {
  animation: rotation 2s infinite linear;
}



@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}


  

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{
  background-color: var(--COLOR_BODY);
  padding: 10px;
}
h1{
  color: var(--COLOR_TEXT);
  display: block;
  font-size: 1.5em;
  margin-block-start: 0.67em;
  margin-block-end: 0.67em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}
h2{
  color: var(--COLOR_TEXT);
}
h3{
  color: var(--COLOR_TEXT);
}
p{
  color: var(--COLOR_TEXT);
}
span{
  color: var(--COLOR_TEXT);
}
label{
  color:  var(--COLOR_TEXT);
}

li{
  color:  var(--COLOR_TEXT);
}
.nav-ctrl{
  background-color:  var(--COLOR_BODY);
  width: 175px;
  height: 175px;
  display: block;
  float:left;
  margin-right:10px;

}

.nav-ctrl figure{

}

.nav-ctrl figure figcaption{
  display: block;
  text-align: center;
  width: 100px
}
.nav-ctrl figure img{
  width: 100px;
  height: 100px;
}
.w_ctrl_add{


}
.w_ctrl_add figcaption {
  color: var(--COLOR_LINK);
}

/* unvisited link */
a:link {
  color: var(--COLOR_LINK);
}

/* visited link */
a:visited {
  color: var(--COLOR_LINK_VISITED);
}

/* mouse over link */
a:hover {
  color: var(--COLOR_LINK_HOVER);
}

/* selected link */
a:active {
  color: var(--COLOR_LINK_ACTIVE);
}

.hidden{display: none;}
/* Cannot set a style like this as it effects all elements UL/LI everywhere. Have to be more specific,
like .my-container ul li {something}
ul { list-style-type: none; margin: 0; padding: 0;  }
li { margin: 5px; padding: 5px; width: 150px; } */

.results ul { 
  list-style-type: none; 
  margin: 0; 
  padding: 0;  
  margin-left: 10px;
  margin-bottom: 50px;
}

.results li { 
  margin-bottom: 20px;
}

.panel{
  background-color: var(--COLOR_PANEL_BG);
  min-height: 500;
  border-color: var(--COLOR_PANEL_BRD);
  border-style: solid;
  border-width: 2;
  overflow: auto;
}

.w_container{
  background-color: var(--COLOR_WIDGET_BG);
  border-radius: 5%;
  border: 1px;
  border-style: solid;
  border-color: var(--COLOR_WIDGET_BRD);

  min-width: 300px;
  min-height: 300px;
  overflow: auto;
  margin: 10px;
  padding-left: 0px !important;
  
  float: left;
}
.w_maximize{
  height: 100%;
}

.widget{
  overflow: auto;
  padding: 5px;
}

.w_title{
  color: white;
}
.widget li a{
  color: var(--COLOR_TEXT);
}

/* .hide-controls div{
  display: none;
} */
.off div{
  display: none;
}

.lib-tbl{
  width: 100%;
}
.lib-tbl tr:nth-child(odd) td{
  background-color: var(--COLOR_LIBRARY_TABLE_ROW_BG);
}
.lib-tbl tr:nth-child(even) td{
  background-color: var(--COLOR_LIBRARY_TABLE_ROW_BG_ALT);
}

.lib-tbl tr th{
color: var(--COLOR_TEXT);
}

.lib-tbl tr:nth-child(even):hover td{
  background-color: var(--COLOR_LIBRARY_TABLE_ROW_HOVER);
}
.lib-tbl tr:nth-child(odd):hover td{
  background-color: var(--COLOR_LIBRARY_TABLE_ROW_HOVER);
}