button {
    border: 1px;
    border-radius: 5%;
    border-color: var(--COLOR_BODY);
    border-style: solid;
}
button:hover {
    cursor: pointer;
    background-color: #ccc;
}
button:disabled{
border: 1px;
border-radius: 5%;
border-color: var(--COLOR_BUTTON_BOARDER);
border-style: solid;
background-color: var(--COLOR_WIDGET_BG);
}
button figure figcaption span{
color: var(--COLOR_LINK);
}
button figure figcaption{
color: var(--COLOR_LINK);
}

