.btn-delete{
    line-height: 12px;
    font-size: 8pt;
    font-family: tahoma;
    margin-top: 1px;
    margin-right: 2px;
    position:absolute;
    top:0;
    right:0;
    background-color: grey;
}
.btn-save {
    line-height: 12px;
    cursor:pointer;
    margin-top: 1px;
    background-color: grey;
    color: #fff;
    text-align: center;
    position:absolute;
    top:0;
    right: 0;
}
.btn-box
{
    position:absolute;
    top:0;
    right: 0;
    cursor: pointer;
    width: 100%;
}
.btn-box button {
    position: relative;
    float: right;
    height: 30px;
}

#contentForm label {
  display: grid;
}